/*
 *  Hero split banner
 *    Styles for the hero split banner section.
 *
 *  Version:
 *    1.0.0 - 2021/09/05
 */

.c-hero-split {
  // Inner
  &__inner {
    position: relative;
  }

  // Banner image
  &__image {
    &.has-mobile.is-desktop { display: none; }

    &-container {
      display: block;
      padding-bottom: #{'min(64%, 30rem)'};

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Featured Product
  &__featured {
    &-container {
      padding: 0.5rem $s-gutter--edge-small 6.125rem;
      background-color: $s-color--background-focus;
    }

    // Image
    &-image {
      mix-blend-mode: darken;

      &-container{
        display: block;

        &.no-pointer{
          cursor: default;
        }
      }

      &-wrapper {
        width: 79.74%;
        margin: 0 auto 0.5rem;
      }
    }

    // Heading & Description
    &-title, &-subtitle {
      text-align: center;
    }

    // Heading
    &-title {
      margin-bottom: 0.875rem;
    }

    &-subtitle {
      margin-bottom: 0;
    }
  }

  // Call to action
  &__cta {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 1.625rem;
    position: relative;
    transform: translateY(50%);

    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none
    }

    &-wrapper {
      @include t-absolute-center-x;
      bottom: 0;
      width: 9.375rem;
      height: 7.8125rem;
      z-index: $s-z--text-overlay;
    }

    &-label {
      display: inline-block;
      font-size: 1.125rem;
      color: $s-color--font-invert;
      text-align: center;
      text-decoration: underline;
      transform: translateY(0.3125rem);
      z-index: 1;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__columns {
      display: flex;
      flex-direction: row-reverse;
    }

    &__image-wrapper, &__featured-container {
      flex-basis: 50%;
    }

    &__image {
      &.is-mobile { display: none; }
      &.has-mobile.is-desktop { display: block; }

      &-container {
        padding-bottom: #{'min(92%, 57.5rem)'};
        height: 100%;
      }
    }

    &__featured {
      &-container {
        padding: 2rem 4.75rem 4.75rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }

      &-image-wrapper {
        width: 58.7%;
        max-width: 36.6875rem;
        margin-bottom: 0.0625rem;
      }
    }

    &__cta {
      transform: none;

      &-wrapper {
        top: 50%;
        bottom: initial;
        transform: translate(-50%, -50%);
      }

      &-label {
        text-decoration: none;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__featured {
      &-image-wrapper {
        margin-bottom: 2.875rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__featured {
      &-container {
        padding: 3.4375rem 13.1875rem 11.75rem 12.375rem;
      }

      &-image-wrapper {
        width: 100%;
        margin-bottom: 0.0625rem;
      }

      &-title {
        margin-bottom: 1.125rem;
      }
    }

    &__cta {
      padding: 0 3rem;

      &-wrapper {
        top: calc(50% - 1.75rem);
        width: 13.25rem;
        height: 10.6875rem;
      }

      &-label {
        font-size: 1.5rem;
      }
    }
  }


}
